/* src/styles.css */

.auth-container {
   background-color:bg-gray-800;
}
.auth-container-inner {
    display: flex; /* Use Flexbox for layout */
    flex-direction: row; /* Stack children vertically */
    align-items: center; /* Center items horizontally */
    text-align: center; /* Center text */
    justify-content:flex-end;
    margin-left:auto;
    margin-top: 20px; /* Space above the inner container */
}
.auth-word{
    display: flex; /* Use Flexbox for layout */
    flex-direction: column; /* Stack children vertically */
    align-items: center; /* Center items horizontally */
}


  
img:hover {
    background-color: #20a050;
    cursor: pointer;
  }
  
img span {
    color: white;
  }
h1{
    font-size: 27px;
    margin-bottom: 10px;
    color: aliceblue;
}
h2{
    font-size: px;
    padding: 2px;
    margin-left: 2%;
    overflow-wrap: break-word;

  
}
.logout-button {
    width: 150px;
  height: 40px;
  background-color: #1ed760;
  border-radius: 20px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: bold;
   cursor: pointer; /* Pointer cursor on hover */
    transition: background-color 0.3s;
    margin-left: auto;
    margin-right: 20px; /* Smooth background color transition */
}

.login-button:hover, .logout-button:hover {
    background-color: #0056b3; /* Darker shade on hover */
}

.login-button{
    width: 150px;
  height: 40px;
  background-color: #1ed760;
  border-radius: 20px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: bold;
   cursor: pointer; /* Pointer cursor on hover */
   margin-left: auto;
   margin-right: 20px;
}